<template>
  <div class="adaptlist">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="6">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>入住適應暨心理社會評估</span>
            </h2>
          </v-col>
          <v-col cols="12" md="6">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd
                :viewData="viewData"
                @getAllData="getAllData"
                ref="editProcess"
                v-model="edititem"
              ></popupadd>
              <!-- <v-btn icon @click.stop="print">
            <router-link :to="{name:'printmember'}" target="_blank" style="text-decoration:none;">
              <v-icon color="success">mdi-printer</v-icon>
            </router-link>
              </v-btn>-->
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
        <v-data-iterator
          :items="viewData"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="mt-2 text-no-wrap">
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th rowspan="3">
                      <!-- <div class="custom-control custom-checkbox mr-2">
                    <input
                      type="checkbox"
                      class="custom-control-input all_select"
                      name="all_select_adapt"
                      id="all_select_adapt"
                    />
                    <label class="custom-control-label" for="all_select_adapt">全選</label>
                      </div>-->
                    </th>
                    <th rowspan="2">評估日期</th>
                    <th rowspan="2">睡眠</th>
                    <th colspan="6">營養狀態</th>
                    <th colspan="6">社會心理</th>
                    <th rowspan="2">入住適應評估結果</th>
                    <th rowspan="2" class="ced_man_key">紀錄人</th>
                    <th rowspan="2" class="ced_man_key">修改紀錄</th>
                  </tr>
                  <tr>
                    <th>營養途徑</th>
                    <th>其他-內容</th>
                    <th>進食型態</th>
                    <th>食量</th>
                    <th>體重</th>
                    <th>BMI</th>
                    <th>表情面容</th>
                    <th>情緒狀態</th>
                    <th>親友聯繫</th>
                    <th>人際互動情形</th>
                    <th>與人相處的行為表現</th>
                    <th>活動參與意願</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item,index) in props.items" :key="index">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn color="primary" dark v-on="on">功能</v-btn>
                        </template>
                        <v-list>
                          <v-list-item @click="edit(item.snkey)">
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item @click="del(item.snkey)">
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td>{{item.date}}</td>
                    <td>{{item.sleep}}</td>
                    <td>{{item.way}}</td>
                    <td>{{item.way_content}}</td>
                    <td>{{item.state}}</td>
                    <td>{{item.quantity}}</td>
                    <td>{{item.weight}}</td>
                    <td>{{item.bmi}}</td>
                    <td>{{item.expression}}</td>
                    <td>{{item.feeling}}</td>
                    <td>{{item.relatives}}</td>
                    <td>{{item.interactive}}</td>
                    <td>{{item.behavior}}</td>
                    <td>{{item.participate}}</td>
                    <td>{{item.evaluate}}</td>
                    <td>{{item.create_man}}</td>
                    <td>{{item.edit_man}}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>  
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/document/AdaptAdd";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10
    };
  },
  mounted() {
    this.getAllData();
  },
  methods: {
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/adapt",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.items = this.viewData = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依name排序
      this.viewData = this.viewData.sort(function(a, b) {
        return parseInt(a.date) < parseInt(b.date) ? 1 : -1;
      });
    },
    edit(snkey) {
      //修改功能
      this.viewData.some((item, index) => {
        if (item.snkey == snkey) {
          // console.log(this.viewData[index])
          this.edititem = this.viewData[index];
          this.$refs.editProcess.$emit("fromParent");
        }
      });
    },
    del(snkey) {
      //刪除功能
      var delData = {};
      this.viewData.some((item, index) => {
        if (item.snkey == snkey) {
          delData = this.viewData[index];
        }
      });

      this.$confirm("確認刪除?").then(res => {
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          snkey: delData.snkey,
          info:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")(" +
            delData.snkey +
            ")(" +
            delData.date +
            ")"
          //刪除的人(刪除時間)(刪除的id)(刪除的日期)
        };

        if (res) {
          this.axios
            .post(
              "general/del/" + this.$store.state.databaseName + "/adapt",
              qs.stringify(postData)
            )
            .then(rs => {
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    }
  }
};
</script>


<style lang="scss">
.adaptlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>