<template>
  <div class="adaptadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <table class="table table-bordered table-sm bg-light">
              <tr>
                <td class="form_title align-middle" colspan="4">評估日期</td>
                <td>
                  <v-text-field type="date" v-model="list.date" :rules="emptyRules"></v-text-field>
                </td>
              </tr>
              <!-- 入住適應暨心理社會評估 -->
              <tr>
                <td class="form_title" rowspan="13">
                  <span class="vertical-mode">入住適應暨心理社會評估</span>
                </td>
                <td class="form_title" colspan="2">睡眠</td>
                <td>0.正常 1.斷續睡眠 2.失眠 3.日夜顛倒 4.紊亂</td>
                <td>
                  <select class="form-control" id="sleep" name="sleep" v-model="list.sleep">
                    <option>0.正常</option>
                    <option>1.斷續睡眠</option>
                    <option>2.失眠</option>
                    <option>3.日夜顛倒</option>
                    <option>4.紊亂</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title" rowspan="5">營養狀態</td>
                <td>營養途徑</td>
                <td>1.口 2.鼻胃管 3.胃造瘻 4.其他</td>
                <td>
                  <select class="form-control" id="way" name="way" v-model="list.way">
                    <option>1.口</option>
                    <option>2.鼻胃管</option>
                    <option>3.胃造瘻</option>
                    <option>4.其他</option>
                  </select>
                  <input
                    type="text"
                    id="way_content"
                    name="way_content"
                    class="form-control"
                    placeholder="內容"
                    v-model="list.way_content"
                    v-if="list.way=='4.其他'"
                  />
                </td>
              </tr>
              <tr>
                <td>進食型態</td>
                <td>1.一般 2.軟質 3.細碎 4.糊狀 5.自製流質 6.配方流質</td>
                <td>
                  <select class="form-control" id="state" name="state" v-model="list.state">
                    <option>1.一般</option>
                    <option>2.軟質</option>
                    <option>3.細碎</option>
                    <option>4.糊狀</option>
                    <option>5.自製流質</option>
                    <option>6.配方流質</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>食量</td>
                <td>每餐可進食份量：1.全部 2.約 1/2 3.&lt; 1/4</td>
                <td>
                  <select
                    class="form-control"
                    id="quantity"
                    name="quantity"
                    v-model="list.quantity"
                  >
                    <option>1.全部</option>
                    <option>2.約 1/2</option>
                    <option>3.&lt; 1/4</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>體重</td>
                <td>( )Kg</td>
                <td>
                  <input
                    type="text"
                    id="weight"
                    name="weight"
                    class="form-control"
                    placeholder="Kg"
                    v-model="list.weight"
                  />
                </td>
              </tr>
              <tr>
                <td>BMI</td>
                <td>
                  體重(公斤) / 身高
                  <sup>2</sup>(公尺
                  <sup>2</sup>)
                </td>
                <td>
                  <input
                    type="text"
                    :value="bmi"
                    class="form-control"
                    placeholder="BMI值"
                    readonly
                  />
                </td>
              </tr>

              <!-- 社會心理 -->
              <tr>
                <td class="form_title" rowspan="6">社會心理</td>
                <td>表情面容</td>
                <td>0.平和 1.笑意 2.呆滯 3.愁苦 4.僵硬 5.其他 6.無法評估</td>
                <td>
                  <select
                    class="form-control"
                    id="expression"
                    name="expression"
                    v-model="list.expression"
                  >
                    <option>0.平和</option>
                    <option>1.笑意</option>
                    <option>2.呆滯</option>
                    <option>3.愁苦</option>
                    <option>4.僵硬</option>
                    <option>5.其他</option>
                    <option>6.無法評估</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>情緒狀態</td>
                <td>0.平穩 1.低沉 2.高昂 3.緊張 4.焦慮 5.憂鬱 6.躁動 7.無法評估</td>
                <td>
                  <select class="form-control" id="feeling" name="feeling" v-model="list.feeling">
                    <option>0.平穩</option>
                    <option>1.低沉</option>
                    <option>2.高昂</option>
                    <option>3.緊張</option>
                    <option>4.焦慮</option>
                    <option>5.憂鬱</option>
                    <option>6.躁動</option>
                    <option>7.無法評估</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>親友聯繫</td>
                <td>1.經常有聯繫(探訪 > 每週1次) 2.偶爾有聯繫(探訪 > 每月1次) 3.鮮少有聯繫 4.其他</td>
                <td>
                  <select
                    class="form-control"
                    id="relatives"
                    name="relatives"
                    v-model="list.relatives"
                  >
                    <option>1.經常有聯繫</option>
                    <option>2.偶爾有聯繫</option>
                    <option>3.鮮少有聯繫</option>
                    <option>4.其他</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>人際互動情形</td>
                <td>1.主動與他人互動 2.引導下可與他人互動 3.不願與他人互動 4.其他</td>
                <td>
                  <select
                    class="form-control"
                    id="interactive"
                    name="interactive"
                    v-model="list.interactive"
                  >
                    <option>1.主動與他人互動</option>
                    <option>2.引導下可與他人互動</option>
                    <option>3.不願與他人互動</option>
                    <option>4.其他</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>與人相處的行為表現</td>
                <td>1.和睦 2.平淡 3.疏離 4.言語攻擊 5.身體攻擊 6.其他</td>
                <td>
                  <select
                    class="form-control"
                    id="behavior"
                    name="behavior"
                    v-model="list.behavior"
                  >
                    <option>1.和睦</option>
                    <option>2.平淡</option>
                    <option>3.疏離</option>
                    <option>4.言語攻擊</option>
                    <option>5.身體攻擊</option>
                    <option>6.其他</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>活動參與意願</td>
                <td>1.主動投入 2.引導下可參與 3.被動配合 4.拒絕參與 5.其他</td>
                <td>
                  <select
                    class="form-control"
                    id="participate"
                    name="participate"
                    v-model="list.participate"
                  >
                    <option>1.主動投入</option>
                    <option>2.引導下可參與</option>
                    <option>3.被動配合</option>
                    <option>4.拒絕參與</option>
                    <option>5.其他</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td colspan="2" class="form_title">入住適應評估結果</td>
                <td>1.良好 2.平順 3.尚可 4.不佳</td>
                <td>
                  <select
                    class="form-control"
                    id="evaluate"
                    name="evaluate"
                    v-model="list.evaluate"
                  >
                    <option>1.良好</option>
                    <option>2.平順</option>
                    <option>3.尚可</option>
                    <option>4.不佳</option>
                  </select>
                </td>
              </tr>
            </table>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'">確認修改</v-btn>
        </v-card-actions>
         <!--<pre>{{list}}</pre>--> 
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"]
    };
  },
  props: ["edititem", "viewData"],
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function() {
      this.dialog = true;
      this.editProcess();
    });
  },
  model: {
    prop: "edititem"
    // event: "parentData"
  },
  updated() {
    // if (this.list.username == "" || this.list.username == undefined) {
    //   this.$nextTick(() => {
    //     this.$refs.username.focus();
    //     this.$refs.form.resetValidation();
    //   });
    // }
  },
  watch: {
    edititem: [
      {
        handler: "editProcess"
      }
    ]
  },
  computed: {
    bmi() {
      console.log("bmi")
      var bmi = 0;
      var height = this.$store.state.uData.height;
      var weight = this.list.weight;
      // ibw = 22*(parseInt(height)/100)*(parseInt(height)/100);
      bmi =
        parseInt(weight) /
        ((parseInt(height) / 100) * (parseInt(height) / 100));
      this.list.bmi = bmi.toFixed(2);
      return this.list.bmi
    }
  },
  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = {};

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      this.list = { ...this.viewData[0] };
      this.list.snkey = "";
      this.list.date = "";
      this.list.edit_man = "";
    },
    editProcess() {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = this.edititem; //將頁面資料與父項的資料連結
      this.list = { ...this.edititem }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/adapt",
            qs.stringify(this.list)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "error" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              // this.$refs.form.resetValidation();
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/adapt",
            qs.stringify(this.list)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              // this.$emit("parentData", this.list);
            }
          });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";

.form_title {
  font-size: 1.2rem;
  text-align: center;
}
.vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
</style>